import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LightProviders, LightHttp, LightJsonUtil } from './../../light/light-out/light-out.module';
import { Http } from '@angular/http';
import { ThrowStmt } from '@angular/compiler';
import { DomSanitizer } from '@angular/platform-browser';


declare let wx;

@Component({
  selector: 'page-wechat',
  templateUrl: 'wechat.html'
})
export class WechatPage {

  chooseImg: any;
  userInfo: any;

  constructor(
    public navCtrl: NavController,
    public lightProviders: LightProviders,
    public _DomSanitizationService: DomSanitizer,
    public lightHttp: LightHttp,

  ) {
  }

  public ngOnInit(): void {

    // this.http.get('http://172.16.50.81:3000/getUserInfo', {
    //   withCredentials: true
    // }).map(res => {
    //   let data = res.json();
    //   alert(res)
    //   switch (data['code']) {
    //     case 0:
    //       console.log(data['userInfo']);
    //       this.userInfo = data['userInfo'];
    //       break;
    //     case 1:
    //       console.log(data['err']);
    //       break;
    //     default:
    //       break;
    //   }
    // }).toPromise();
  }

  public wxChooseImage() {
    let that = this;
    wx.chooseImage({
      count: 1, // 默认9
      sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
      sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
      defaultCameraMode: "normal", //表示进入拍照界面的默认模式，目前有normal与batch两种选择，normal表示普通单拍模式，batch表示连拍模式，不传该参数则为normal模式。（注：用户进入拍照界面仍然可自由切换两种模式）
      success: function (res) {
          var localIds = res.localIds; // 返回选定照片的本地ID列表，
          // andriod中localId可以作为img标签的src属性显示图片；
          // 而在IOS中需通过上面的接口getLocalImgData获取图片base64数据，从而用于img标签的显示
          if(that.lightProviders.lightPlatform)
          wx.getLocalImgData({
            localId: localIds[0], // 图片的localID
            success: function (res2) {
              let localData = res2.localData;
              if(!localData.split(',')[1]){
                localData = 'data:image/jpeg;base64,'+localData;//安卓系统获取到的base64数据没有'data:image/jpeg;base64,'前缀，所以必须手动加上
                }else{
                localData = localData.replace('jgp', 'jpeg');
              }
              that.chooseImg = localData; // localData是图片的base64数据，可以用img标签显示
          }
        });
      }
    });
  }

  public async wxPreviewFile() {
    let code = "DD4238B71FE34E0FAF572F103DAD4EA2";
    let [attachList] = await Promise.all([
      this.lightHttp.get("Attachment/GetAttachments", { objectID: code }),
    ]);
    attachList = LightJsonUtil.toObject(attachList) as Array<any>;
    console.log(attachList)
    let FilesLength = attachList[0]['FilesLength'];
    let Name = attachList[0]['Name'];
    let Path = attachList[0]['Path'];
    wx.previewFile({
      url: "http://wechat.xianyumgc.cn/附件：徒步报名表.docx", // 需要预览文件的地址(必填，可以使用相对路径)
      // name: Name, // 需要预览文件的文件名(不填的话取url的最后部分)
      size: 12795 // 需要预览文件的字节大小(必填)
    });
  }

}



