import { Injectable } from '@angular/core';
import { Platform, IonicApp, Events, Keyboard, NavController, ToastController } from 'ionic-angular';
import { LightPop } from './lightPop';
import { LightMinimize } from '../../light-nation/light-nation.module';

//退出对象的信息
@Injectable()
export class LightExit {

  public backButtonPressed = false;

  private timeOutOp: any;

  /**
   * 退出的操作信息
   * @param {Platform} platform
   * @param {LightPop} lightPop
   * @memberof LightExit
   */
  constructor(
    private platform: Platform,
    private events: Events,
    private keyboard: Keyboard,
    public lightMinimize: LightMinimize,
    private lightPop: LightPop,
    private toastCtrl: ToastController
  ) {

  }

  /**
   * app的退出操作
   *
   * @memberof LightExit
   */
  public appExit(): void {
    this.lightPop.confirm("是否退出程序", "退出").then(() => {
      this.platform.exitApp();
    }).catch(() => {

    });
  }

  //双击退出提示框
  private minimizeOrExit() {
    if (this.backButtonPressed) { //当触发标志为true时，即2秒内双击返回按键则退出APP
      this.platform.exitApp();
    } else {
      this.backButtonPressed = true;
      let toast = this.toastCtrl.create({
        message: '再按一次退出应用',
        duration: 2000,
        position: 'bottom',
        cssClass: 'toast-exit'
      });
      toast.present();
      setTimeout(() => {
        this.backButtonPressed = false;
      }, 4000);//3秒内没有再次点击返回则将触发标志标记为false
    }
  }

  /**
   * 安卓物理返回按键处理
   */
  registerBackButtonAction(ionicApp: IonicApp, navCtrl: NavController) {
    if (this.platform.is('ios')) {
      return;
    }
    this.platform.registerBackButtonAction(() => {
      this.events.publish('android:backButtonAction');
      if (this.keyboard.isOpen()) { // 如果键盘开启则隐藏键盘
        this.keyboard.close();
        return;
      }
      // 如果想点击返回按钮隐藏toast或loading或Overlay就把下面加上
      // this.ionicApp._toastPortal.getActive() ||this.ionicApp._loadingPortal.getActive()|| this.ionicApp._overlayPortal.getActive()
      const activePortal = ionicApp._modalPortal.getActive() || ionicApp._overlayPortal.getActive();
      if (activePortal) {
        activePortal.dismiss();
        activePortal.onDidDismiss(() => { });
        return;
      }
      const childNav = navCtrl.getActiveChildNavs()[0]; // 获取tabs导航,this.nav是总导航,tabs是子导航
      if (!childNav) {
        this.minimizeOrExit()
        return;
      }
      const tab = childNav.getSelected(); // 获取选中的tab
      const activeVC = tab.getActive(); // 通过当前选中的tab获取ViewController
      const activeNav = activeVC.getNav(); // 通过当前视图的ViewController获取的NavController
      return activeNav.canGoBack() ? activeNav.pop() : this.minimizeOrExit();
    }, 1);

  }

}



